.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#search-container {
  display: flex;
  width: 100%;
  box-sizing: border-box;
  border: 2px solid rgba(0, 0, 0, 0.16);
  background-color: #fff;
  border-radius: 4px;
  padding: 8px 18px;
  margin-bottom: 20px;
  &:focus-within {
    border: 2px solid #0095ffcc;
  }
  #search-icon {
    width: 14px;
    margin-right: 12px;
    opacity: 0.25;
    &.active {
      opacity: 1;
    }
  }
  #search-input {
    flex-grow: 1;
    &::placeholder {
      opacity: 0.4;
      // font-weight: 500;
    }
  }
}
